.details {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  padding: 20px;
}

.image {
  height: 200px;
}

.info {
  width: 56%;
  border: solid 1px rgb(133, 130, 130);
  margin-left: 0.5%;
}

.address {
  width: 13%;
  margin-left: 0.3%;
  border: solid 2px rgb(133, 130, 130);
}

.root-container {
  margin: 5px auto;
}

span {
  text-decoration: underline;
}

fieldset > div {
  line-height: 21px;
}

legend {
  font-weight: 100;
}

.user-name-page {
  padding: 20px;
}

h2 {
  margin-left: 20px;
}
